.bottomNav { 
    height : fit-content ; 
    padding : 1rem 2rem ; 
    
    @include position-center  ;
    display : flex ; 
    flex-direction: row ;
    justify-content: space-between; 
    position: fixed ; 
    bottom  : 0 ; 
    font-size : 2.5rem  ; 
    background : hsl(103 , 50% , 99% ) ; 
    box-shadow: 0 0 3px hsla(0, 0%, 80% , .3 )   ;
    padding-top : 1rem ; 
    
    @media( max-width : 650px ) { 
        font-size : 2rem ; 
    }

    @media (max-width : 470px ) { 
        font-size : 1.4rem ; 
    }
    &__item { 
        display: flex ;
        flex-direction: column ;
        align-items: center ; 
        outline :none ;
        text-decoration: none ; 
        color : black ; 
        position : relative ; 

        // &::after { 
        //     content : '' ; 
        //     position: absolute ; ; 
        //     bottom : -3px  ; 
        //     left : 0 ;
            
        //     transition: width .3s linear  ;
        //     width : 0px ; 
        //     height : 3px ; 
        //     border-radius: 3px ;
        //     background : currentColor ; 

        // }

        // &:hover::after , 
        // &.active::after {
        //      width : 100% ; 
        // }
        &:hover , 
        &:active  ,
        &.active 
        { 
            color : blue ; 
        }

       
        svg { 
            font-size : 3rem ;
            margin-right : 1rem ;  
           
            @media( max-width : 650px ) { 
                font-size : 2.5rem ; 
            }
        
            @media (max-width : 470px ) { 
                font-size : 2rem ; 
                margin-right : 3px 
            }

        }


    }
}