.projects-block { 
    
    .s-title { 
        margin-bottom : 2rem ; 
    }
}


.projects { 
    
    .project {
        display: flex ;
        flex-direction: column ; 
        padding : 2rem 1rem ; 
        border-radius: 3rem ;
        box-shadow : 0 0 3px  hsla(0 , 50% , 90%) ; 
        margin-bottom : 2rem ; 

        @include respond-to('min' , 600px ) { 
            flex-direction: row ;
        }



        &__image { 
            overflow:  hidden ; 
            border-radius: 3rem ; 
            margin : 0 auto 2rem auto ; 
            width : 95% ; 
            height : 300px ;
            box-shadow : 0 0 5px hsla(0 , 50% , 90%) ;
            
            @include respond-to('min' , 600px ) {
                flex-basis: 50% ;
                flex-direction: row ;
            }

            @include respond-to('min' , 1000px ) { 
                flex-basis : 45% 
            }
            img { 
                width : 100% ; 
                height : 100% ; 

            }
        }
        &__content {
            @include respond-to('min' , 600px ) {
                flex : 1 1 50%;  
                flex-direction: row ;
                padding : 0 3rem ; 
            }
            &--title { 
                font-size : 2.5rem ; 
                font-weight : 300 ;
                margin-bottom: 1.5rem ;

            }
            &--description { 
                font-size : 1.4rem ; 
                line-height: 170% ;
                
            }

            &--cta { 
                margin : 2rem 0 ;
                display: inline-block;

                & > a { 
                    display: inline-block;

                }

            }
        }
    }
}