.top { 
    height : 10vh ; 
    position : absolute ; 
    width : 95vw ;
    top : 0 ; 
    left :50% ; 
    transform: translateX(-50%); 
    display: flex ; 
    justify-content: space-between ; 
    align-items : center  ;

    @include respond-to("min" , 650px) { 
        width: 90vw ; 
    }

    @include respond-to("min" , 900px) { 
        width: 80vw ; 
    }
    &__title { 
        font-size : 3rem ; 
        font-weight: 300 ;
        text-transform: uppercase ;

    }

    &__theme-toggler { 
        width : 3rem ; 
        height : 3rem ; 
        background : transparent ; 

        & > svg { 
            font-size : 2.5rem ; 
        }
    }
}