@mixin respond-to ($type , $breakpoint) { 
    @if ($type == 'max' ) { 
        @media (max-width : $breakpoint ) { 
            @content ; 
        }
    }
    @else { 
        @media (min-width : $breakpoint ) { 
            @content ;
        }
    }
}



//USED TO CENTER POSITION ITEMS 
@mixin position-center { 
    width : 95vw ;
    left :50% ; 
    transform: translateX(-50%); 

    @include respond-to("min" , 650px) { 
        width: 90vw ; 
    }

    @include respond-to("min" , 900px) { 
        width: 80vw ; 
    }
}
