//general styling for padding of layout 
.position-center { 
    
    width : 95% ; 
    margin : 0 auto ; 
    padding : 2rem 0;  

    @include respond-to('min' , 650px ) { 
        width : 80% 
    }
    @include respond-to('min' , 900px) { 
        width : 80% ; 
    }
    

}

//SECTION TITLE 
.s-title { 
    font-size : 3rem ; 
    font-weight : 300 ; 

}

//to emphasize texts 
.important { 
    font-weight : 700 ; 

}

//remove outline and border 
.no-outline-border { 
    outline : none ; 
    border : none ; 
}