.home { 
    padding : 2rem 0 ; ;
    
    &__greeting { 
        font-size : 1.6rem ; 
        line-height : 3rem ; 

    }

    &__name { 
        margin-top : 2rem ;
        
        h1 { 
            font-size : 3rem ; 
            font-weight : 300 ; 
            text-transform: uppercase ;
        }
        & > span { 
            display: block; 
            margin-top: .8rem ;
            font-size : 1.6rem ; 
          
        }
    }

    &__roles { 
        font-size : 1.4rem ; 
        margin-top : 2rem ; 
        list-style-type: '-' ;
        list-style-position :inside  ;

        li { 
            margin : 1rem 0 ; 
        }
    }
}


.about { 
    padding : 2rem 0 ; 

    &__content { 
        font-size : 1.4rem ; 
        text-align: justify; 
        hyphens: auto; 
        line-height: 150% ;
        p  { 
            margin : 2rem 0 ; 
        }
    }
}